import { Stepper, Step, StepLabel } from "@mui/material";

import InfoIcon from "@mui/icons-material/Info";
import FamilyRestroomIcon from "@mui/icons-material/FamilyRestroom";
import ConnectWithoutContactIcon from "@mui/icons-material/ConnectWithoutContact";
import ContactsIcon from "@mui/icons-material/Contacts";

import "./steps.css";

const steps = [
	{
		label: "Click on My Information",
		desc: "Click on My Information in the Navbar and click on My Dependents",
		icon: <InfoIcon style={{ color: "#024a92" }} />
	},
	{
		label: "Choose Member",
		desc: "Click on the member from the list whose service is required",
		icon: <FamilyRestroomIcon style={{ color: "#024a92" }} />
	},
	{
		label: "Click on Member Service",
		desc: "Click on Contact Member Service button on the top right corner",
		icon: <ContactsIcon style={{ color: "#024a92" }} />
	}
];

const Steps = (): JSX.Element => {
	return (
		<div className="steps-container">
			<div className="steps-header">
				Follow the alternate steps to <span className="contact-member">Contact Member Service</span>
			</div>
			<Stepper orientation="vertical" activeStep={5}>
				{steps.map((step, index) => {
					return (
						<Step key={index}>
							<StepLabel icon={step.icon}>
								<>
									<div className="step-label">{step.label}</div>
									<div className="step-desc">{step.desc}</div>
								</>
							</StepLabel>
						</Step>
					);
				})}
			</Stepper>
		</div>
	);
};

export default Steps;
